// Called when the Gatsby browser runtime first starts.
exports.onClientEntry = () => {
  // docz broken in IE/Edge:
  // - https://github.com/doczjs/docz/issues/249
  // - https://github.com/doczjs/docz/search?q=scrollTo&type=Code
  if (typeof Element !== 'undfined' && !Element.prototype.scrollTo) {
    Element.prototype.scrollTo = function(left, top) {
      this.scrollTop = top
      this.scrollLeft = left
    }
  }
}
